<template>
    <div class="checkHouse" >
        <navigation-bar pageName="验房" @onLeftClick="goBack">
        </navigation-bar>
        <div class="search-box">
            <div class="input-box" v-show="true">
                <input type="text" placeholder="楼座号" v-model="building">
                <input type="text" placeholder="单元号" v-model="unit">
                <input type="text" placeholder="房间号" v-model="room">
            </div>
            <div class="search" @click="search">搜索</div>
        </div>
        <div class="roomsList" v-show="roomsList && roomsList.length > 0">
            <van-list
                v-model="loading"
                :finished="finished"
                :finished-text="finishedText"
                :immediate-check="false"
                @load="loadMore">
                <div class="item" v-for="(item,index) in roomsList" :key="index">
                    <div class="title">
                        <img :src="floorIcon" alt="">
                        <div>{{item.BUILD_NAME}}-{{item.UNIT_NO}}</div>
                    </div>
                    <div class="cell-box">
                        <van-swipe-cell v-for="(room,index2) in item.ROOM_RESULT" :key="index2" :stop-propagation="false">
                            <div class="room"  @click="goCheckHouseList(room)">
                                <div class="status">
                                    <img :src="getStatusIcon(room.DELIVE_FLAG)" alt="">
                                </div>
                                <div class="name">{{room.ROOM_NAME}}</div>
                            </div>
                            <template slot="right">
                                <div class="btn-box">
                                    <div class="repair" v-if="room.DELIVE_FLAG == '' || room.DELIVE_FLAG == '0'" @click="repair(item, room)">报修</div>
                                    <div class="check" v-if="room.DELIVE_FLAG == '0' || room.DELIVE_FLAG == ''" @click="checkHouse(item, room)">验房</div>
                                    <div class="blank" v-if="room.DELIVE_FLAG == '1'"></div>
                                    <div class="detail" v-if="room.DELIVE_FLAG == '1'" @click="getDetail(item, room)">详情</div>
                                </div>
                            </template>
                        </van-swipe-cell>
                    </div>
                </div>
            </van-list>
        </div>
        <div class="no-list" v-show="!roomsList || roomsList.length == 0">
          <img :src="noSourceImg" alt="">
          <div>暂无房间信息</div>
        </div>
    </div>
</template>

<script>
import { getRoomsList } from "../../services/checkHouseService";
import { store } from "../../utils/store";
import floorIcon from "@/assets/imgs/checkHouse/Floor.png";
import checkIcon from "@/assets/imgs/checkHouse/checked.png";
import repairIcon from "@/assets/imgs/checkHouse/repaired.png";
import stayCheckIcon from "@/assets/imgs/checkHouse/stayCheck.png";
import noSourceImg from "@/assets/imgs/workPlan/noSource.png";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "checkHouse",
  components: { NavigationBar },
  data() {
    return {
      activeTab: 0,

      // 上拉加载是否处于加载状态
      loading: false,
      page: 1,
      rows: 5,
      // 上拉加载是否加载完成
      finished: false,
      finishedText: "我是有底线的",

      roomsList: [],
      building: "",
      unit: "",
      room: "",

      floorIcon: floorIcon,
      checkIcon: checkIcon,
      repairIcon: repairIcon,
      stayCheckIcon: stayCheckIcon,
      noSourceImg: noSourceImg,
      userInfo: null,
      scrollTop: 0
    };
  },
  activated: function() {
    //  0使用缓存， 1刷新页面  2.刷新单条数据
    var reloadCheckHouseStatus = store.get("reloadCheckHouseStatus");
    console.log(reloadCheckHouseStatus);
    if (reloadCheckHouseStatus == 2) {
      // 刷新单条
      this.updateSingleData(store.get("checkListSingleData"));
      document.querySelector(".roomsList").scrollTop = this.scrollTop;
      store.dele("reloadCheckHouseStatus");
      store.set("checkListSingleData", "");
    } else if (
      !(reloadCheckHouseStatus == undefined) &&
      reloadCheckHouseStatus == 0
    ) {
      document.querySelector(".roomsList").scrollTop = this.scrollTop;
      store.dele("reloadCheckHouseStatus");
    } else {
      this.resetCheckHouseStatus();
      this.getRoomsList();
      store.dele("reloadCheckHouseStatus");
      document.querySelector(".roomsList").scrollTop = this.scrollTop = 0;
    }
  },
  beforeRouteLeave(to, from, next) {
    if (to.name != "home") {
      this.scrollTop = document.querySelector(".roomsList").scrollTop;
    }
    next();
  },
  methods: {
    resetCheckHouseStatus: function() {
        this.loading = false;
        this.page = 1;
        this.rows = 5;
        this.finished = false;
        this.roomsList = [];
        this.building = "";
        this.unit = "";
        this.room = "";
    },
    /**
     * 获取状态图片
     */
    getStatusIcon: function(status) {
      switch (status) {
        case "0":
          return this.repairIcon;
        case "1":
          return this.checkIcon;
        case "":
          return this.stayCheckIcon;
      }
    },

    /**
     * 获取楼栋信息
     */
    getRoomsList: function() {
      this.loading = true;
      var data = {
        AREA_CODE: store.get("userInfo").AREA_CODE,
        page: this.page + "",
        rows: this.rows + "",
        ROOM_NAME: this.room,
        UNIT_NO: this.unit,
        BUILD_NAME: this.building
      };
      getRoomsList({
        data: JSON.stringify(data)
      }).then(
        res => {
          this.setRoomsList(res);
        },
        error => {
          this.loading = false;
          this.$toast(error);
          this.finished = true;
          this.finishedText = "";
        }
      );
    },

    /**
     * 设置展示的验房列表信息
     */
    setRoomsList(res) {
      this.loading = false;
      this.finished = res.TOTAL <= this.page * this.rows;
      if (this.page == 1) {
        this.roomsList = res.ROWS ? res.ROWS : [];
      } else {
        this.roomsList = this.roomsList.concat(res.ROWS ? res.ROWS : []);
      }
      if (this.page == 1 && this.finished) {
        this.finished = true;
        this.finishedText = "";
      }
    },

    /**
     * 上拉加载
     */
    loadMore: function() {
      this.page++;
      this.getRoomsList();
    },

    search: function() {
      document.querySelector(".roomsList").scrollTop = this.scrollTop = 0;
      this.page = 1;
      this.getRoomsList();
    },

    /**
     * 验房按钮
     */
    checkHouse: function(item, room) {
      this.$router.push({
        name: "handCheck",
        params: {
          BUILD_CODE: item.BUILD_CODE,
          BUILD_NAME: item.BUILD_NAME,
          ROOM_CODE: room.ROOM_CODE,
          ROOM_NAME: room.ROOM_NAME,
          DELIVE_FLAG: room.DELIVE_FLAG,
          UNIT_NO: item.UNIT_NO
        }
      });
    },

    /**
     * 获取详情
     */
    getDetail: function(item, room) {
      this.$router.push({
        name: "checkDetail",
        params: {
          BUILD_CODE: item.BUILD_CODE,
          BUILD_NAME: item.BUILD_NAME,
          ROOM_CODE: room.ROOM_CODE,
          ROOM_NAME: room.ROOM_NAME,
          DELIVE_FLAG: room.DELIVE_FLAG,
          UNIT_NO: item.UNIT_NO
        }
      });
    },

    /**
     * 报修
     */
    repair: function(item, room) {
      this.$router.push({
        name: "newRepair",
        params: {
          BUILD_CODE: item.BUILD_CODE,
          BUILD_NAME: item.BUILD_NAME,
          ROOM_CODE: room.ROOM_CODE,
          ROOM_NAME: room.ROOM_NAME,
          DELIVE_FLAG: room.DELIVE_FLAG,
          UNIT_NO: item.UNIT_NO
        }
      });
    },

    /**
     * 验房列表
     */
    goCheckHouseList: function(room) {
      store.set("checkHouseWorkRoomCode", room.ROOM_CODE);
      this.$router.push({
        name: "checkHouseWork"
      });
    },

    /**
     * 页面返回事件
     */
    goBack: function() {
      this.scrollTop = 0;
      this.$router.back(-1);
    },

    /**
     * 更新列表中的单条数据
     */
    updateSingleData: function(data) {
      // 更新这个值DELIVE_FLAG  已验房是1  报修中是0
      for (var i = 0; i < this.roomsList.length; i++) {
        for (var j = 0; j < this.roomsList[i].ROOM_RESULT.length; j++) {
          if (this.roomsList[i].ROOM_RESULT[j].ROOM_CODE == data.ROOM_CODE) {
            this.roomsList[i].ROOM_RESULT[j].DELIVE_FLAG = data.DELIVE_FLAG;
            return;
          }
        }
      }
    }
  },
  computed: {}
};
</script>
